.carousel {
  padding: 98px 0 170px;
  width: 100vw;
  &__gallery {
    z-index: 1;
    margin-bottom: 75px;
    @media (max-width: 991.98px) {
      margin-bottom: 40px;
    }
  }

  &__slide {
    position: relative;
    max-width: 100vw;
    height: 100%;

    &:not(:last-child) {
      margin-right: 20px;
      @media (max-width: 575.98px) {
        margin-right: 30px;
      }
    }
    img {
      border-radius: 6px;
    }

    @media (max-width: 1199.98px) {
      max-width: 90vw;
    }
  }

  &__descr {
    position: absolute;
    bottom: 24%;
    left: 1vw;
    font-family: 'M PLUS 1p', sans-serif;
    font-weight: 500;
    font-size: 80px;
    line-height: 85px;
    @media (max-width: 991.98px) {
      bottom: 15%;
      font-size: 60px;
    }
    @media (max-width: 767.98px) {
      font-size: 40px;
    }
    @media (max-width: 575.98px) {
      bottom: 25%;
    }
  }

  &__arrows {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 32px;
  }

  &__prev,
  &__next {
    &:hover {
      svg path {
        fill: #75ff72;
      }
    }
  }

  @media (max-width: 991.98px) {
    padding-bottom: 85px;
  }
}
